import uploadImg from '../../../common/uploadImg.js'

import topBar from '../../components/topBar.vue'
export default {
  components: {
    topBar: topBar
  },
  data () {
    return {
      formData: {
        article_title: '',
        article_content: '',
        category_id: '',
        tags: '',
        address: '',
        address_latitude: '',
        address_longitude: '',
        uploaded: [],
        source: 'umi'
      },
      imgArr: [],
      imgUploadID: [],
      time: 0,
      lists: '',               // 请求回来的数据
      array: '',               // picker需要话题列表['啦啦啦', '哈哈哈']
      index: '',               // picker选中的话题索引值
    }
  },
  onLoad(params) {
    if(params.type) {
      this.formData.source= params.type
    }
    this.getTopicList()
  },
  methods: {
    goIndex() {
      uni.switchTab({
        url: '../index'
      })
    },
    // 获取所有话题列表
    getTopicList() {
      let that= this,
        params= {
          url: that.$api.topicAllList
        }
        that.$httpRequest(params).then(res => {
          console.log(res.data.list)
          let lists= res.data.list,
            arr= []
          that.lists= lists
          lists.map((item, index) => {
            arr.push(item.name)
          })
          that.array= arr
        })
    },
    
    // 选择话题改变
    bindPickerChange: function(e) {
      console.log('picker发送选择改变，携带值为', e.target.value)
      let index= e.target.value
      this.index = index
      this.formData.category_id= that.lists[index].category_id
    },
    
    // 获取当前位置信息
    getAddress() {
      let that= this
      uni.chooseLocation({
        success: function (res) {
          that.formData.address= res.address + res.name
          that.formData.address_latitude= res.latitude
          that.formData.address_longitude= res.longitude
        }
      })
    },
    
    // 删除图片
    delImg(index) {
      this.imgArr.splice(index, 1)
      this.formData.uploaded.splice(index, 1)
    },
    // 上传图片
    uploadImg () {
      let that= this
      let length= this.imgArr.length
      let leavelength = 9 - length
      if(leavelength > 0) {
        
        uni.chooseImage({
          count: leavelength, //默认9
          sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
          sourceType: ['album'], //从相册选择
          success: function (res) {
            
            let time= 0,
              max_time= res.tempFilePaths.length
            uni.showLoading({
                title: '上传图片中..'
            })
            // console.log(JSON.stringify(res.tempFilePaths))
            res.tempFilePaths.map((item, index) => {
              // 上传图片
              uploadImg(item).then((res)=> {
                time ++
                if(time === max_time) {
                  uni.hideLoading()
                  uni.showToast({
                    title: max_time + '张图片上传成功',
                    icon: 'none'
                  })
                }
                that.imgArr.push(item)
                console.log(JSON.parse(res).data.file_id)
                that.formData.uploaded.push(JSON.parse(res).data.file_id)
                
              })
            }, err => {
              uni.hideLoading()
              uni.showToast({
                title: '第' + (index + 1) + '张图片上传失败',
                icon: 'none'
              })
            })
          }
        }) 
      } else {
        uni.showToast({
          title: '最多只能传9张图片哦',
          icon: 'none'
        })
      }
    },
    // 发布文章
    releaseAction() {
      let that= this
      if(!that.formData.article_content) {
        uni.showToast({
          title: '文章内容不能为空！',
          icon: 'none'
        })
        return
      }
      uni.showLoading({
        title: '发布中'
      })
      let params= {
        url: that.$api.articleRelease,
        data: {
          formData: JSON.stringify(that.formData)
        },
        method: 'POST'
      }
      that.$httpRequest(params).then(res => {
        uni.hideLoading()
        if(res.code === 1) {
          
          uni.showToast({
            title: '发布成功,即将自动返回',
            icon: 'none'
          })
          that.goIndex()
        } else {
          uni.showToast({
            title: res.msg,
            icon: 'none'
          })
          return
        }
      })
    }
  }
}
